body{background-color:#ebdceb;margin:0 auto}div{margin:0;padding:0;text-align:center;font-size:18px;font-family:sans-serif}h1,h2,h3{font-family:sans-serif;font-size:42px;font-weight:600;color:#af6daf}h2{font-size:28px;padding-top:0}h3{font-size:18px;padding-top:6px}.game-container{position:relative;width:955px;margin:0 auto}.game-header{display:flex;justify-content:space-between;max-width:655px;align-items:flex-end;margin:0 auto;padding-top:40px}.game-header h1,.game-header h3{line-height:0}.game-header a{color:inherit;text-decoration:none}.game-header a:hover{text-decoration:underline}.control-buttons{position:absolute;z-index:5;left:150px;top:100px;width:655px;display:flex;justify-content:space-between}.restart-game{line-height:42px;background-color:#6b4c6b;font-weight:600;color:white;border-radius:5px;transition:0.3s ease all;border:2px solid #6b4c6b;box-sizing:border-box;width:180px}.restart-game:hover{cursor:pointer;background-color:#d8b9d8;color:#6b4c6b}.nav-links{border:1px solid #6b4c6b;background-color:#ffefff;border-radius:5px;display:flex}.nav-links>ul{list-style:none;margin:0;padding:12px;display:flex;justify-content:space-between;font-size:16px}li{padding:0 8px;border-right:1px solid gray}li:last-child{border:none}.nav-links a{text-decoration:none;color:#6b4c6b;transition:0.3s ease all}.nav-links a:hover{text-decoration:underline}.modal-trigger{width:40px;height:40px;border:2px solid #6b4c6b;background-color:#ffefff;color:#6b4c6b;font-size:36px;font-weight:bold;line-height:40px;border-radius:50%;transition:0.3s ease all;cursor:pointer}#modal-trigger:hover,#modal-trigger:focus{background-color:#d8b9d8;cursor:pointer}.modal{display:none;position:fixed;padding-top:50px;left:0;top:0;width:100%;height:100%;background-color:#000;background-color:rgba(0,0,0,0.5);z-index:10}.modal-content{position:relative;background-color:white;padding-right:20px;padding-bottom:20px;margin:auto;width:655px;border:1px solid thistle;border-radius:5px;box-shadow:0 0 10px gray;z-index:11}.modal-content ol li{text-align:left;padding:5px 0 5px;font-size:16px;border:none;color:#404040;font-weight:bold}ol{margin:0}li>ol{list-style:lower-alpha}.modal-content li>ol>li{font-weight:400}.close-button{padding:0;position:absolute;right:15px;font-size:28px;width:28px;color:#404040;top:15px;z-index:13;cursor:pointer}#close-button:hover,#close-button:focus{color:gray;transition:0.3 ease all;cursor:pointer}.show{display:block}
